<template>
  <v-app>
    <v-toolbar color="primary" dark>
      <v-toolbar-side-icon @click="dialog=true"></v-toolbar-side-icon>
      <v-toolbar-title>停车场管理系统</v-toolbar-title>
    </v-toolbar>
    <v-content>
      <v-container>
        <v-card>
          <v-card-title>启动</v-card-title>
          <v-card-text>
            <v-text-field v-model="num" label="请输入车牌号" @input="img = false"></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-layout justify-end>
              <v-btn color="primary" @click="check">查询</v-btn>
            </v-layout>
          </v-card-actions>
        </v-card>
        <img v-if="img" :src="`/img/${num}`" class="mt-5" @click="submit" style="cursor:pointer">
      </v-container>
      <v-dialog v-model="dialog" fullscreen>
        <v-card title>
          <v-card-title primary-title>
            <v-btn icon @click="dialog = false">
              <v-icon>arrow_back</v-icon>
            </v-btn>
            <h3>全部车辆信息</h3>
          </v-card-title>
          <v-data-table :headers="[{text:'车牌号',value:'num'}]" :items="fileList" hide-actions>
            <template slot="items" slot-scope="props">
              <tr style="cursor:pointer">
                <td>{{ props.item }}</td>
              </tr>
            </template>
          </v-data-table>
        </v-card>
      </v-dialog>
    </v-content>
  </v-app>

</template>

<script>
import axios from 'axios'
export default {
  name: 'app',
  data() {
    return {
      num: "",
      img: false,
      fileList: [],
      dialog: false,
    }
  },
  async mounted() {
    this.fileList = (await axios.get('/img')).data
  },
  methods: {
    async check() {
      try {
        await axios.get('/img/' + this.num.toString())
        this.img = true
      } catch (e) {
        alert('未获取到车牌信息')
      }
    },
    async submit() {
      try {
        await axios.post('/img/' + this.num.toString())
        alert('启动成功')
      } catch (e) {
        alert('启动失败:' + e.toString())
      }
    }
  },
}
</script>

